import { useRecoilState } from 'recoil';
import { postsState, isUserState } from '../../../library/atom';
import WriteModal from '../../../components/Modals/writeModal';
import { useState } from 'react';
import styles from './search.module.css';

const Search = ({
  posts,
  selected,
  setSelected,
  recruit,
  setRecruit,
  writer,
  setWriter,
  myPosts,
  setMyPosts,
  setPosts,
  locationList,
}) => {
  const [isLogin, setIsLogin] = useRecoilState(isUserState);
  const locationNameList = ['', '', ''];
  const recruitList = ['', ''];
  const [modalOpen, setModalOpen] = useState(false);

  //
  const openModal = () => {
    setModalOpen(true);
  };
  const closeModal = () => {
    setModalOpen(false);
  };

  const handleSelect = e => {
    setSelected(e.target.value);
  };
  const handleRecruit = e => {
    setRecruit(e.target.value);
  };
  const handleWriter = e => {
    setWriter(e.target.value);
  };
  const handleMyPosts = e => {
    setMyPosts(!myPosts);
  };
  for (let i = 0; i < posts.length; i++) {
    for (let key in posts[i]) {
      if (key === 'locationName') {
        if (locationNameList.indexOf(posts[i][key]) === -1)
          locationNameList.push(posts[i][key]);
      }
    }
  }
  return (
    <>
      <section className={styles.search__container}>
        <div className={styles.container__col2}>
          <div className={styles.selectBox}>
            <select
              onChange={handleSelect}
              value={selected}
              className={styles.select1}
            >
              <option value={''}>----</option>
              {locationNameList.map((el, idx) => {
                return (
                  <option key={idx} value={el}>
                    {el}
                  </option>
                );
              })}
            </select>
          </div>
          <div className={styles.selectBox}>
            <select
              onChange={handleRecruit}
              value={recruit}
              className={styles.select2}
            >
              <option value={''}>----</option>
              {recruitList.map((el, idx) => {
                return (
                  <option
                    key={idx}
                    value={el === '' ? 'recruiting' : 'recruited'}
                  >
                    {el}
                  </option>
                );
              })}
            </select>
          </div>
          <input
            value={writer}
            onChange={handleWriter}
            placeholder=''
            className={styles.writer}
          ></input>
        </div>
        {isLogin && (
          <div className={styles.container__col1}>
            <button
              className={styles.myPosts}
              onClick={handleMyPosts}
              type='button'
            >
              {myPosts === true ? '' : ''}
            </button>
            <button className={styles.writeBtn} onClick={openModal}>
              
            </button>

            <WriteModal
              locationList={locationList}
              open={modalOpen}
              close={closeModal}
              header=''
              setPosts={setPosts}
            ></WriteModal>
          </div>
        )}
      </section>
    </>
  );
};

export default Search;
