import { useState, useEffect } from "react";
import { Map, MapMarker } from "react-kakao-maps-sdk";

const MapContainer = (props) => {
  const [state, setState] = useState({
    //   
    center: { lat: 37.49676871972202, lng: 127.02474726969814 },
    //    panto ( )
    isPanto: true,
  });
  const [searchAddress, SetSearchAddress] = useState();

  //        
  const SearchMap = () => {
    const ps = new window.kakao.maps.services.Places();
    const placesSearchCB = function (data, status, pagination) {
      if (status === window.kakao.maps.services.Status.OK) {
        const newSearch = data[0];
        setState({
          center: { lat: newSearch.y, lng: newSearch.x },
        });
      }
    };
    ps.keywordSearch(`${searchAddress}`, placesSearchCB);
  };

  const handleSearchAddress = (e) => {
    SetSearchAddress(e.target.value);
  };
  console.log(props.data.data);

  return (
    <>
      {console.log(props.data.data)}

      <Map
        center={props.data.data.point[0]}
        isPanto={state.isPanto}
        style={{
          width: "500px",
          height: "500px",
          position: "fixed",
          top: "50%",
          left: "50%",
          transform: "translate(-50%, -50%)",
        }}
        level={7}
      >
        {props.data.data.point.map((e, i) => (
          <MapMarker key={i} position={e}>
            <div key={i} style={{ color: "#000" }}>
              {props.data.data.location[i]}
            </div>
          </MapMarker>
        ))}
      </Map>
      {/* <div>
        <input onChange={handleSearchAddress}></input>
        <button onClick={SearchMap}></button>
      </div> */}
    </>
  );
};

export default MapContainer;
